.form-page {
    display: flex;
    flex-direction: column;
}

.form-page__title-wrapper {
    
    margin-left: auto;
    
    margin: 0;
}

.form-page__title {
    margin: 0;
    margin-top: 27px;

    font-size: 36px;
    line-height: 36px;
    font-family: "Oswald", "Arial", sans-serif;
    font-weight: 400;
    text-align: left;

    @media (min-width: $tablet-width) {
        margin-top: 60px;
        font-size: 60px;
        line-height: 60px;
    }
    @media (min-width: $desktop-width) {
        margin-top: 68px;
    }   
}

.form-page__description {

    margin: 0;
    margin-top: 37px;

    max-width: 228px;
    
    font-size: 14px;
    line-height: 18px;
    
    font-family: "Oswald", "Arial", sans-serif;
    font-weight: 400;
    text-transform: uppercase;
   
    @media (min-width: $tablet-width) {
        margin-top: 53px;
        margin-left: 40px;
        max-width: 326px;;
        font-size: 20px;
        line-height: 30px;
    }

    @media (min-width: $desktop-width) {
        margin-top: 59px;
        margin-left: 0;
    }
}